import common from "../../common/common.js"
import HMmessages from "../../components/HM-messages/HMmessages.vue"
import TColorPicker from "../../components/t-color-picker/t-color-picker";
import UploadImages from "../../components/colorui/components/uploadImages";
var serverUrl = common.serverUrl
var _self;
export default {
	components:{
		HMmessages,
		TColorPicker,
		UploadImages
	},
	data() {
		return {
			isPost:false,
			imgUrl:"",
			tempFace: "",
			userInfo:{},
			token:'',
			type:'',
			imgs:[],
			color: {
				r: 255,
				g: 0,
				b: 0,
				a: 0.6
			},
			isEdit: false,
			fontColor: '#000',
			formats: {},
			readOnly: false,
			placeholder: '描述下你的旅行心得,帮助更多旅行的人...',
			editorHeight: 300,
			keyboardHeight: 0,
			isIOS: false
		};
	},
	onLoad() {
		_self = this;
		var userInfo = uni.getStorageSync("globalUser")
		if(userInfo!=null&&userInfo!=""&&userInfo!=undefined){
			this.userInfo=userInfo
			this.token="Bearer "+this.userInfo.token
			this.type=this.userInfo.tokenType
			console.log(userInfo)
		}
	},

	methods: {
		upload(){

			// if(this.imgs.length===0){
			// 	this.HMmessages.show("成功",{iconColor:'#ffffff',fontColor:'#ffffff', background:"#ffd655"})
			// }
			var i = 0
			var length = this.imgs.length
			this.upLoadApi(i,length)
		},
		upLoadApi(i,length){
			var userInfo =this.getGlobalUser("globalUser")
			uni.uploadFile({
				url:serverUrl+'/oss/insert?parentId='+userInfo.userId,
				filePath:this.imgs[i],
				name:"file",
				success: (uploadFileRes) => {
					var response = JSON.parse(uploadFileRes.data)
					console.log(response.data)
					if(response.code===0){
						i++
						console.log(i)
					if (i < length) {
						// debugger
						this.upLoadApi(i,length);//upLoadApi undefined
					}else{
						return
					}
				}
				},
				complete(){}
				}
		)},

		// 重新选择头像
		changePendingFace(){
			uni.chooseImage({
				count:9,
				sizeType:["compressed"],
				sourceType:["album","camera"],
				success: (res) => {
					//获得临时路径
				// var tempFilePath = res.tempFilePaths[0]
				// console.log(res.tempFilePaths)
				// this.imgUrl=tempFilePath
				// console.log(this.imgUrl)

				this.imgs.push(res.tempFilePaths[0])
				console.log(this.imgs)
				}
			})
		},
		cancel() {
			this.isEdit = false;
		},
		open() {
			this.$refs.colorPicker.open();
			this.isEdit = true;
			// uni.hideKeyboard();
		},
		hideKey() {
			uni.hideKeyboard();
		},
		async confirm(e) {
			this.isEdit = false;
			this.fontColor = await e.hex;
			this.onStatusChange({
				detail: {
					color: e.hex
				}
			});
			this.$forceUpdate();
		},
		readOnlyChange() {
			this.readOnly = !this.readOnly
		},
		onEditorReady() {
			uni.createSelectorQuery().select('#editor').context(function(res) {
				_self.editorCtx = res.context;
			}).exec();
		},
		undo() {
			this.editorCtx.undo();
		},

		redo() {
			this.editorCtx.redo();
		},

		blur() {
			this.editorCtx.blur();
		},

		format(e) {
			// this.hideKey();
			let {
				name,
				value
			} = e.target.dataset;
			if (!name) return; // console.log('format', name, value)
			this.editorCtx.format(name, value);
		},

		onStatusChange(e) {
			this.formats = e.detail;
		},

		insertDivider() {
			this.editorCtx.insertDivider({
				success: function() {
					console.log('insert divider success');
				}
			});
		},

		store(e) {
			this.editorCtx.getContents({
				success: function(res) {
					e.currentTarget.id == 1 ? console.log('保存内容:', res.html) : uni.navigateTo({
						url: `./components/preview?rich=${encodeURIComponent(res.html)}`
					});
				}
			});
		},

		clear() {
			this.editorCtx.clear({
				success: function(res) {
					console.log("clear success");
				}
			});
		},

		removeFormat() {
			this.editorCtx.removeFormat();
		},

		insertDate() {
			const date = new Date();
			const formatDate = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`;
			this.editorCtx.insertText({
				text: formatDate
			});
		},

		insertImage() {
			// const that = this;
			uni.chooseImage({
				count: 1,
				success: function(res) {
					_self.editorCtx.insertImage({
						src: res.tempFilePaths[0],
						data: {
							id: 'abcd',
							role: 'god'
						},
						width: '80%',
						success: function() {
							console.log('insert image success');
						}
					});
				}
			});
		}
		}
}
